.containerBienvenue{
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    margin: 0 auto;
    .containerName{
        position: absolute;
        display: flex;
        justify-content: center;
        flex-direction: column;
        height: 500px;
        width: 500px;
        font-size: 16px;
      
        @media screen and (max-width : 1100px)
        {
           
            left: 150px;
        }
        @media screen and (max-width : 700px)
        {
            font-size: 14px;
            left: 100px;
        }
        @media screen and (max-width : 580px)
        {
            font-size: 11.5px;
            height: 450px;
             width: 450px;
             left: 80px;
        }
        @media screen and (max-width :380px)
        {
            font-size: 10px;
            height: 350px;
             width: 350px;
             left: 70px;
        }
    
        h1{
            font-size: 1em;
            color: $color-1;
          
        }
        h2{
            font-size: 3.1em;
            color: $color-3;
            position: relative;
            width:349px;
        @media screen and (max-width : 700px)
        {
            width:305px;
        }
        @media screen and (max-width : 580px)
        {
            width:260px;
        }
        @media screen and (max-width :380px)
        {
            width:220px;
        }
            
            &::before{
                content: "";
                position: absolute;
                height: 3.2vw;
                width: 100%;
                height: 100%;
                left:100%;
                background:$color-2;
                border-left: 2px solid $color-3;
                animation: typing1 2s steps(12) forwards;
                @keyframes typing1 {
                    0%{
                        left: 0;
                        width: 100%;
                    }
                    100%{
                        transform: translateX(100%);
                        width: 0%;
                    }
                }
            }
        }
        h4{
            font-size: 1.5em;
            color: $color-3;
            position: relative;
            width: 387px;
            @media screen and (max-width : 700px)
            {
                width:340px;
            }
            @media screen and (max-width : 580px)
            {
                width:285px;
            }
            @media screen and (max-width :380px)
            {
                width:238px;
            }
            &::before{
                content: "";
                position: absolute;
                height: 3.2vw;
                width: 100%;
                height: 100%;
                left: 100%;
                background:$color-2;
                border-left: 2px solid $color-3;
                animation: typing2 2s steps(16) forwards;
                @keyframes typing2 {
                    0%{
                        left: 0;
                        width: 100%;
                    }
                    100%{
                        transform: translateX(100%);
                        width: 0%;
                    }

                }
            }
            
        }

        




    }
}


