.gridProject{
    height: 600px;
    width: 900px;
    position: absolute;
    top: 500px;
    display: grid;
    grid-template-columns: repeat(3, 300px);
    grid-template-rows: repeat(3, 350px);
    gap: 15px;
    justify-content: center;
    @media screen and (max-width : 1100px)
    {
        grid-template-columns: repeat(2, 300px);
        top: 500px;
        height: 1450px;
        width: 700px;
    }
    @media screen and (max-width : 833px)
        {
            grid-template-columns: repeat(2, 250px);
            height: 1400px;
            width: 500px;
        }
    
    @media screen and (max-width : 730px)
    {
        grid-template-columns: repeat(1, 300px);
        position: absolute;
        top: 500px;
        height: 2100px;
        width: 500px;
    
    }
    @media screen and (max-width : 620px)
        {
            height: 2200px;
            width: 350px;
            top: 650px;
        }
        @media screen and (max-width : 620px)
        {
            height: 2200px;
            width: 350px;
            top: 650px;
        }
        @media screen and (max-width : 380px)
        {
            height: 2200px;
            width: 300px;
            top: 650px;
            left: 30px;
        }
  

    .p1,.p2,.p3,.p4,.p5,.p6{
        background: $color-6;
        border-radius: 10px;
        transition: 0.25s all;
       
        .img1, .img2{
            img{
                position: absolute;
                height: 7%;
                width: 7%;
                right: 10px;
                bottom: 10px;
               
            }
            img:nth-child(1){
                opacity: 1;
                z-index: 10;
                &:hover{
                    opacity: 0;
                    cursor: pointer;
                }
            }
            img:nth-child(2){
                opacity: 1;
            }
        }
        h6{
            position: absolute;
            bottom: 0;
            margin: 5px;
        }
        .img2{
            img{
                position: absolute;
                height: 7%;
                width: 7%;
                right: 40px;
                bottom: 10px;
               
            }
        }
    }
    .p1{
        filter: opacity(0.6) drop-shadow(0 0 0 $color-3);
        &:hover{
            transition: 0.25s all;
            transform: translateX(-10px);
             filter: none;
        }
        text-align: center;
        img{
            height: 70%;
            width: 300px;
            border-radius: 10px;
            @media screen and (max-width : 833px)
                {
                    width: 250px;
                }
        }
        h3{
            margin-bottom: 10px;
        }
        h5{
            color :$color-3;
        }
        
        
