.miniResumeContainer{
    height: 100vh;
    width: 100%;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    color: $color-1;
    .miniResume{
       height: 400px;
       width: 900px;
       font-size: 20px;
      
       @media screen and (max-width : 1100px)
        {
            height: 500px;
            width: 700px;
            font-size: 16px;
        }
        @media screen and (max-width : 820px)
        {
            height: 400px;
            width: 500px;
            font-size: 13px;
        }
        @media screen and (max-width : 620px)
        {
            height: 100px;
            width: 350px;
            font-size: 12px;
        }
        @media screen and (max-width :380px)
        {
            width: 320px;
            font-size: 12px;
        }
       h1{
        font-size: 2em;
        letter-spacing: 1px;
        margin: 15px;
        position: relative;
        &::after{
            position: absolute;
            content: "";
            width: 660px;
            opacity: 0.3;
            height: 2px;
            background: $color-4;
            top: 50%;
            left: 200px;
        @media screen and (max-width : 1100px)
        {
            width: 460px;
            left: 200px;
        }
        @media screen and (max-width : 820px)
        {
            width: 310px;
            left: 150px;
        }
        @media screen and (max-width : 620px)
        {
            width: 175px;
            left: 150px;
        }
        @media screen and (max-width :380px)
        {
            width: 165px;
            left: 140px;
        }
        }
       }
       .descriptionContainerEN{
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 10px;
        
        position: relative;
        @media screen and (max-width : 620px)
                {
                    height: 950px;
                }
            .experiences{
                height: 800px;
                border: 2px solid $color-4;
                border-top-left-radius: 10px;
                margin: 5px;
                width: 450px;
                font-size: 1.1em;
                 @media screen and (max-width : 1100px)
                {
                    height: 650px;
                   
                }
                @media screen and (max-width : 820px)
                {
                    height: 600px;
                    top: 44px;
                }
                @media screen and (max-width : 620px)
                {
                    position: absolute;
                    width: 320px;
                    border-radius: 10px;
                    height: 500px;
                }
                @media screen and (max-width :380px)
                {
                    width: 290px;
                    left: 10px;
                }
                h2{
                    margin: 10px;
                }
                .titres{
                    position: relative;
                    margin-top: 30px;
                    font-weight: lighter;
                   
                    .descriptionEDF, .descriptionORANO, .descriptionIRONHACK{
                        margin: 10px;
                        top: 50px;
                        font-size: 0.9em;
                        span{
                            color: $color-3;
                            cursor: pointer;
                            &:hover{
                                text-decoration: underline;
                            }
                        }
                    }
                    .descriptionEDF{
                        margin-bottom: 30px;
                    }
                }
            }
            .formations{
                height: 800px;
                border: 2px solid $color-4;
                border-top-right-radius: 10px;
                margin: 5px;
                width: 450px;
                font-size: 1.1em;
                position: relative;
                   @media screen and (max-width : 1100px)
                {
                    height: 650px;
                   
                }
                @media screen and (max-width : 820px)
                {
                    height: 600px;
                }
                @media screen and (max-width : 620px)
                {
                    position: absolute;
                    height: 500px;
                    width: 320px;
                    top:600px;
                    border-radius: 10px;
                }
                @media screen and (max-width :380px)
                {
                    width: 290px;
                    left: 10px;
                }
                h2{
                    margin: 10px;
                }
                .titres{
                    position: relative;
                    margin-top: 30px;
                    font-weight: lighter;
                   
                    .udemy, .descriptionPolytech, .descriptionUPMC, .descriptionBac,.ironhack{
                        margin: 10px;
                        top: 50px;
                        font-size: 0.9em;
                        span{
                            color: $color-3;
                            cursor: pointer;
                            &:hover{
                                text-decoration: underline;
                            }
                        }
                    }
                    .udemy, .descriptionPolytech{
                        margin-bottom: 30px;
                    }
                
            }
            }
       }
    }


}